<template lang="pug">
  #home
    .triangle
    jumbotron.jumbotron
    .container
      .first
        .aboutUs
          .aboutTitle.
            <br>
          .aboutContent.
            SAEPA200612
            
            
            10
            
            
        .icon_container
          .icon_box(v-for="item in items")
            img(:src="item.url", :alt="item.alt")
            .icon_text
              p {{ item.text1 }}
              p {{ item.text2 }}
              p 
      .projects
        .intro
          .summary
            img.paddingB(src="../images/book.png")
            div.
              20062006<br>
              <br>
              "Student Association of Educational Poverty Alleviation" SAEPA
          .achievements
            .picSide
              img(src="../images/cx1.png", alt="")
            .textSide.borderTop(:style="{ background: textSides[0] }")
              .textTitle 
              div.
                <br>
                <br>
                
            .picSide
              img(src="../images/cx2.png", alt="")
          .achievements
            .picSide.picLSide
              img(src="../images/dnca1.png", alt="")
            .picSide.picRSide
              img(src="../images/dnca2.png", alt="")
            .textSide(:style="{ background: textSides[1] }")
              .textTitle 
              div.
                <br>
                ++
          .achievements
            .textSide(:style="{ background: textSides[2] }")
              .textTitle 
              div.
                <br>
                <br>
                
            .picSide.picLSide
              img(src="../images/mxkt1.png", alt="")
            .picSide.picRSide
              img(src="../images/mxkt2.png", alt="")
          .achievements
            .picSide
              img(src="../images/zmzj1.png", alt="")
            .textSide(:style="{ background: textSides[3] }")
              .textTitle 
              div.
                <br>
                <br>
                
            .picSide
              img(src="../images/zmzj2.png", alt="")
</template>

<script>
import Jumbotron from './Jumbotron';

export default {
  name: 'home',
  components: {
    Jumbotron,
  },
  data() {
    const tmp = ') no-repeat center center';
    return {
      items: [
        { url: require('../images/5.png'), alt: '', text1: '', text2: '' },
        { url: require('../images/6.png'), alt: '', text1: '', text2: '' },
        { url: require('../images/7.png'), alt: '', text1: '', text2: '' },
      ],
      textSides: [
        'url(' + require('../images/home/text1.jpg') + tmp,
        'url(' + require('../images/home/text2.jpg') + tmp,
        'url(' + require('../images/home/text3.jpg') + tmp,
        'url(' + require('../images/home/text4.jpg') + tmp,
      ],
    };
  },
};
</script>

<style lang="scss" scoped>
  .triangle {
    border-left: 100px solid transparent;
    border-right: 100px solid transparent;
    border-top: 40px solid #f8f9fb;
    position: absolute;
    top: 80px;
    left: calc(100vw / 2 - 100px);
    z-index: 5;
  }

  .first {
    padding: 30px 100px;
    .aboutUs {
      text-align: center;
      .aboutTitle {
        padding: 30px 0;
        font-size: 30px;
        color: #612379;
      }
      .aboutContent {
        font-size: 16px;
        line-height: 25px;
        padding-bottom: 30px;
        color: #646466;
      }
    }
    .icon_container {
      font-size: 18px;
      color: #612379;
      margin-top: 70px;
      display: flex;
      justify-content: center;
      .icon_box {
        text-align: center;
        padding: 0 55px;
        img {
          width: 85%;
        }
      }
    }
  }

  .paddingB {
    padding-bottom: 50px;
  }

  .intro {
    .summary {
      text-align: center;
      font-size: 20px;
      color: white;
      padding: 70px;
      background: url("../images/1.jpg") no-repeat center center;
      background-size: cover;
      -webkit-background-size: cover;
      img {
        width: 150px;
        height: 100px;
      }
    }
    .achievements {
      display: flex;
      .picSide, .textSide {
        height: calc(100vw / 3 * 280 / 400);
      }
      .picSide {
        width: 33.333333%;
        img {
          width: 100%;
        }
        overflow: hidden;
      }
      .picLSide {
        margin-right:0;
      }
      .picRSide {
        margin-left: 0;
      }
      .textSide {
        background-size: cover;
        -webkit-background-size: cover;
        color: white;
        width: 33.333333%;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .textTitle {
          font-size: 25px;
          font-weight: bold;
        }
      }
    }
  }

  @media only screen and (max-width: 768px) {
    .triangle {
      display: none;
    }
    .intro {
      /*margin-top: 40px;*/
      /*padding: 0;*/
      .achievements .picSide {
        padding: 0;
        margin: 0;
      }
      font-size: 11px;
      .achievements .textSide .textTitle {
        font-size: 15px;
      }
      .summary {
        font-size: 14px;
        padding: 50px;
        img {
          width: 70px;
          height: 50px;
          padding: 10px;
        }
      }
    }
    .first {
      padding: 15px;
      .icon_container {
        font-size: 12px;
        margin-top: 40px;
        .icon_box {
          padding: 0;
          img {
            width: 75%;
          }
        }
      }
    }
  }

</style>



